import { Col, Image, Layout, Menu, Row, theme } from "antd";
import { UserOutlined } from '@ant-design/icons';
import { ConfigurationContext } from "providers/ConfigurationProvider";
import { SignInUserContext } from "providers/SignInUserProvider";
import React, { useContext } from "react";
import { Helmet } from 'react-helmet-async';
import { useTranslation } from "react-i18next";
import { postFetcher } from "fetchers/fetchers";
import { useMutation } from "@tanstack/react-query";
import { useNavigate } from "react-router-dom";
import { IModel } from "interfaces/configuration";

const { Header, Content, Sider } = Layout;

interface ICrudContainer {
  title: string;
  breadcrumbs?: JSX.Element | JSX.Element[];
  children: JSX.Element | JSX.Element[];
}

export const CrudContainer: React.FC<ICrudContainer> = ({ title, breadcrumbs, children }) => {
  const navigate = useNavigate();
  const { configuration } = useContext(ConfigurationContext);
  const { signedInUser, signedInUserRefetch } = useContext(SignInUserContext);
  const { t: _t } = useTranslation('App');

  const {
    token: { colorBgContainer, colorPrimary },
  } = theme.useToken();

  const { mutate: mutateSignOut } = useMutation(() => postFetcher("/sign-out", {}), {
    onSuccess: () => {
      signedInUserRefetch();
      navigate('/sign-in');
    },
  });

  const onClickSideBarMenuItem = (item: any) => {
    navigate(`/list/${item.key}`)
  };

  const onClickRightMenuItem = (item: any) => {
    switch (item.key) {
      case 'sign-out':
        mutateSignOut();
        break;
      default:
        break;
    }
  };

  return (
    <>
      <Helmet defaultTitle={title}>
        <meta name="description" content={title} />
      </Helmet>
      <Layout 
        style={{ height: '100vh'}}
      >
        <Header
          style={{ background: colorPrimary, paddingInline: 20 }}
        >
          <Row>
            <Col>
              <Image src={configuration.site_header_logo} preview={false} height={32} alt={configuration.site_name} />
            </Col>
            <Col>
              <span style={{ color: colorBgContainer, fontSize: 20, marginLeft: 10 }}>{configuration.site_name}</span>
            </Col>
            <Col style={{ margin: 'auto' }}></Col>
            <Col>
              <Menu
                style={{ background: colorPrimary, minWidth: 100, color: colorBgContainer }}
                theme="light"
                mode="horizontal"
                items={[
                  {
                    key: signedInUser?.id || 'user',
                    icon: <UserOutlined />,
                    label: signedInUser?.username || _t('User'),
                    children: [
                      {
                        key: 'sign-out',
                        label: _t('Sign Out'),
                      }
                    ]
                  }
                ]}
                onClick={onClickRightMenuItem}
              />
            </Col>
          </Row>
        </Header>
        <Layout>
          <Sider width={200} style={{ background: colorBgContainer }}>
            <Menu
              mode="inline"
              defaultSelectedKeys={['1']}
              style={{  
                borderRight: 0,
                marginTop: 50,
              }}
              items={configuration.models.map((model: IModel) => {
                return {
                  key: model.name,
                  label: model.name,
                }
              })}
              onClick={onClickSideBarMenuItem}
            />
          </Sider>
          <Layout style={{ padding: 16 }}>
            {breadcrumbs}
            <Content
              style={{
                padding: 24,
                marginTop: 16,
                background: colorBgContainer,
              }}
            >
              {children}
            </Content>
          </Layout>
        </Layout>
      </Layout>
    </>
  );
};
